import React from 'react';
import { withRouter } from "react-router";
import html2canvas from 'html2canvas';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import swal from 'sweetalert';


class Header extends React.Component{
    constructor(){
        super();
        this.state = {
            isModalOpen: false,
            feedbackFormData: {feedbackText:'',include_screenshot:true, currentPageScreenshotURL:''}
        }
        this.logout = this.logout.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.toggleFeedbackForm = this.toggleFeedbackForm.bind(this);
    }
    
    toggleFeedbackModal(){
        this.setState({isModalOpen: !this.state.isModalOpen})
    }

    logout(){
        window.localStorage.clear();
        this.props.history.push('/login');
    }

    handleInputChange(event) {
        var target = event.target;
        var value = target.type === 'checkbox' ? target.checked : target.value;
        var name = target.name;

        var newObj = {...this.state.feedbackFormData, [name]:value};
        this.setState({feedbackFormData: newObj})
    }

    sendFeedbackData(){
        var data = {
            feedbackText: this.state.feedbackFormData.feedbackText
        }
        if(this.state.feedbackFormData.include_screenshot){
            data['image'] = this.state.feedbackFormData.currentPageScreenshotURL;
        }

        swal({
          title: "Success",
          text: 'Feedback sent successfully!',
          icon: "success"
        })

        this.toggleFeedbackModal();
    }

    toggleFeedbackForm(){
        this.setState({isModalOpen: !this.state.isModalOpen});
        let newThis = this;
        setTimeout(function(){  
            var pageScreenshotElm = document.getElementById('current-page-screenshot');
            if(pageScreenshotElm){
                html2canvas(document.getElementById('root')).then(function(canvas) {
                    var image = new Image();
                    image.src = canvas.toDataURL();
                    pageScreenshotElm.appendChild(image);   

                    var newObj = {...newThis.state.feedbackFormData, currentPageScreenshotURL:canvas.toDataURL()};

                });
            }
        },500)
    }

    render(){
        let username = JSON.parse(window.localStorage.getItem('user'));
        var currentPageScreenshot = document.getElementById('current-page-screenshot');
        
        if(!this.state.feedbackFormData.include_screenshot){
            if(currentPageScreenshot){
                currentPageScreenshot.children[0].style.display = 'none';
            }
        }else{
            if(currentPageScreenshot){
                currentPageScreenshot.children[0].style.display = 'block';
            }
        }        
        return(
            <>
                {
                    (username && username['username']==='student')?
                        <nav className="navbar navbar-expand-lg navbar-light bg-light">
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
                                <a className="navbar-brand" href="#">Brand</a>
                                <div className='ml-auto'>
                                    <button className="btn btn-outline-success mr-3" onClick={this.toggleFeedbackForm}><i className="fa fa-question-circle"></i></button>
                                    <span>Hello {username['username']}</span>
                                    <button className="btn btn-secondary ml-3" onClick={this.logout}><i className='fa fa-sign-out'></i>Logout</button>
                                </div>
                            </div>
                        </nav>
                    :
                    (username && username['username']==='principal')?
                        <nav className="navbar navbar-expand-lg navbar-light bg-light">
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
                                <a className="navbar-brand" href="#">Brand</a>
                                <div className='ml-auto'>
                                    <button className="btn btn-outline-success mr-3" onClick={this.toggleFeedbackForm}><i className="fa fa-question-circle"></i></button>
                                    <span>Hello {username['username']}</span>
                                    <button className="btn btn-secondary ml-3" onClick={this.logout}><i className='fa fa-sign-out'></i>Logout</button>
                                </div>
                            </div>
                        </nav>
                    :
                    <nav className="navbar navbar-expand-lg navbar-light bg-light">
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
                            <a className="navbar-brand" href="#">Brand</a>

                            <form className="form-inline my-2 my-lg-0 ml-auto">
                                <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search"/>
                                <button className="btn btn-outline-success my-2 my-sm-0" >Search</button>
                                <button className="btn btn-outline-success ml-2" onClick={()=>this.props.history.push('/login')}>Login</button>
                            </form>
                        </div>
                    </nav>
                }
            <Modal isOpen={this.state.isModalOpen}>
                <ModalHeader>Send Feedback</ModalHeader>
                <ModalBody>
                    <textarea className="feedback-text" placeholder="Have feedback? We’d love to hear it, but please don’t share sensitive information. Have questions? Try help or support." name="feedbackText" onChange={this.handleInputChange}></textarea>
                    <p><input type="checkbox" name="include_screenshot" onChange={this.handleInputChange} checked={this.state.feedbackFormData.include_screenshot} /> Include Screenshot</p>
                    <div id="current-page-screenshot"></div>
                    <small>Go to the Legal Help page to request content changes for legal reasons. Some account and system information may be sent to Google. We will use the information you give us to help address technical issues and to improve our services, subject to our Privacy Policy and Terms of Service.</small>
                </ModalBody>
                <ModalFooter>
                    <div className="action-buttons">
                        <button className="btn" onClick={this.toggleFeedbackForm}>Cancel</button>
                        <button className="btn btn-link" onClick={this.sendFeedbackData}>Send</button>
                    </div>
                </ModalFooter>
            </Modal>  
            </>
        );
    }
}

export default withRouter(Header);